<script>
import $ from 'jquery'
//jQueryjQuery
window.jQuery = $;

//
require('../../assets/script/plugins/jquery/pagination')(jQuery)

export default {
  created() {
    //vue
    $(document).ready(function ($) {
      $('.M-box').pagination({
        callback: function (api) {
          $('.now').text(api.getCurrent());
        }
      }, function (api) {
        $('.now').text(api.getCurrent());
      });

      $('.M-box2').pagination({
        coping: true,
        homePage: '',
        endPage: '',
        prevContent: '',
        nextContent: ''
      });

      $('.M-box1').pagination({
        totalData: 100,
        showData: 5,
        coping: true
      });

      $('.M-box3').pagination({
        pageCount: 50,
        jump: true,
        coping: true,
        homePage: '',
        endPage: '',
        prevContent: '',
        nextContent: ''
      });

      $('.M-box4').pagination({
        pageCount: 3, //3
      }, function (api) {
        api.setPageCount(20);//20
      });

      $('.M-box5').pagination({
        pageCount: 50,
        jump: true,
        callback: function (api) {
          var data = {
            page: api.getCurrent(),
            name: 'mss',
            say: 'oh'
          };
          $.getJSON('http://localhost:3000/data.json', data, function (json) {
            console.log(json);
          });
        }
      });
    });
  }
}
</script>
