import Vue from 'vue'
import Vuex from 'vuex'
import VueRouter from 'vue-router'
import App from './App.vue'
import index from './components/Index.vue'
import project from './components/Project.vue'
import photography from './components/Photography.vue'
import message from './components/Message.vue'
import jqueryPagination from './components/plugins/JqueryPagination.vue'
import vuePagination from './components/plugins/VuePagination.vue'
import javascriptSlider from './components/plugins/JavascriptSlider.vue'
import snaker from './components/game/Snaker.vue'

Vue.use(VueRouter)

/**
 * index       => 
 * project     => 
 * photography => 
 * message     => 
 */
const routes = [{
  path: '/',
  component: index,
  meta: {
    title: 'Mss'
  }
}, {
  path: '/project',
  component: project,
  meta: {
    title: ' - Mss'
  }
}, {
  path: '/project/jquery-pagination',
  component: jqueryPagination,
  meta: {
    title: 'pagination.js  jquery'
  }
}, {
  path: '/project/javascript-slider',
  component: javascriptSlider,
  meta: {
    title: 'javasrcript javascript js'
  }
}, {
  path: '/project/vue-pagination',
  component: vuePagination,
  meta: {
    title: 'vue vue'
  }
}, {
  path: '/project/snaker',
  component: snaker,
  meta: {
    title: ''
  }
}, {
  path: '/message',
  component: message,
  meta: {
    title: ' - Mss'
  }
}, {
  path: '/photography',
  component: photography,
  meta: {
    title: ' - Mss'
  }
}]

const router = new VueRouter({
  routes
})

router.beforeEach((to, from, next) => {
  /* title */
  if (to.meta.title) {
    document.title = to.meta.title;
  }
  next();
})

const app = new Vue({
  router,
  render: h => h(App)
}).$mount('#app')



